<template>
  <button class="btn" @click="goTo(redirect)">{{name}}</button>
</template>

<script>
export default {
  name: "Button",
  props: {
      name: String,
      redirect: String,
  },
  methods: {
      goTo(redirect) {
          window.open(redirect)
      }
  }
  
};
</script>

